import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, List, Avatar, Cell, Button, Div, FormStatus, Header, FixedLayou, TabsItem, Tabs, FixedLayout  } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24Message from '@vkontakte/icons/dist/24/message';
import { fireEvent } from "../utils";
import Icon24List from '@vkontakte/icons/dist/24/list';
const Stories = ({ id, back, top, openProfile, go }) => {
    return (
        <Panel id={id}>
            <PanelHeader
            
            >
                Истории
            </PanelHeader>
            <Group>
                <List>
                    {top.map((item) => {
                        return (
                            <Cell
                                before={<Avatar src={item.avatar}/>}
                                description={"Рейтинг: " + item.carma}
                                onClick={() => openProfile(item.id)}
                            >
                                {item.first_name + " " + item.last_name}
                            </Cell>
                        );
                    })}
                </List>
                <Group>
                <Group title="">

                <Div>
                 Вы можете добавить свой сервис, нажав на кнопку снизу. 

                 В каталог нашего сервиса могут попасть только те приложение, которые не нарушают правила. 
                
                 <Button size="xl" onClick={() => fireEvent("https://vk.me/public182752093")} level="secondary">Добавить сервис</Button> 
                </Div>
                </Group>
      <Group title="Полезные ссылки">
                <List>
                    <Cell
                        expandable
                        onClick={() => fireEvent("https://vk.com/pukhn_seo")}
                        before={<Icon24Message fill="#4bb34b" />}
                    >
                        <span style={{ color: "#4bb34b" }}>Сообщить о баге</span>
                    </Cell>
                </List>
            </Group>
            <FixedLayout vertical="bottom">
      <Tabs>
        <TabsItem onClick={() => go("home")}>Каталог</TabsItem>
        <TabsItem  selected={true}>Добавить сервис</TabsItem>
      </Tabs>
    </FixedLayout>
    </Group>
    </Group>
   


  

        </Panel>
    );
};
const mapProps = (state) => ({
    top: state.top
}); 

const mapDispatch = ({ navigator: { goBack }, user: { openProfile } }) => ({
    back: goBack,
    openProfile
});

export default connect(mapProps, mapDispatch)(Stories);