import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, List, Avatar, Cell, Button, Div, FormStatus } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';

const Profile = ({ id, back, top, openProfile }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
              Календарь   
            </PanelHeader>


           <Group title="Утром"> 
            <Div>
        Календарь - очень незаменимая вещь, пригодится всегда! Расчитайте все, что надо не выходя из ВКонтакте.
         </Div>
         </Group>
    

        </Panel>
    );
};

const mapProps = (state) => ({
    top: state.top
});

const mapDispatch = ({ navigator: { goBack }, user: { openProfile } }) => ({
    back: goBack,
    openProfile
});

export default connect(mapProps, mapDispatch)(Profile);