import React from "react";
import { connect } from "react-redux";
import VKConnect from "@vkontakte/vkui-connect-promise";
import { Panel, Group, Avatar, PanelHeader, CellButton, Div, FormStatus, topPlace, List, Switch, Cell, Counter, Tooltip, Button } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Unpin from '@vkontakte/icons/dist/24/unpin';

const Transaction = ({ id, back, transaction, go }) => {
    let date = new Date();
    date.setTime((transaction.time || 0) * 1000);
    return (
        <Panel id={id}>
        <PanelHeader
            left={<PanelHeaderBack onClick={back} />}
        >
           Здоровье
        </PanelHeader>
        
        <Group title="Утром">
      <Div>
        Пейте утром 2 стакана чистой воды, чтобы весь день чувствовать себя отлично. 
        И помните, вода - наше все!
      </Div>
      </Group> 
      <Group title="Перед сном">
      <Div>
      Чтобы хорошо выспаться, прогуляйтесь перед сном. 
      </Div>

    </Group>

    </Panel>
);
};
const mapProps = (state) => ({
    transaction: state.transactions.selected
});

const mapDispatch = ({ navigator: { goBack } }) => ({
    back: goBack
});

export default connect(mapProps, mapDispatch)(Transaction);