import React from "react";
import { connect } from "react-redux";
import { Panel, PanelHeader, Group, List, Avatar, Cell, Button, Div, FormStatus } from "@vkontakte/vkui";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';

const Top = ({ id, back, top, openProfile }) => {
    return (
        <Panel id={id}>
            <PanelHeader
                left={<PanelHeaderBack onClick={back} />}
            >
                Фонарик
            </PanelHeader>
            <Group>
                <List>
                    {top.map((item) => {
                        return (
                            <Cell
                                before={<Avatar src={item.avatar}/>}
                                description={"Рейтинг: " + item.carma}
                                onClick={() => openProfile(item.id)}
                            >
                                {item.first_name + " " + item.last_name}
                            </Cell>
                        );
                    })}



                </List>
            </Group>

            <Group>
      <Div>
        Фонарик - очень полезная вещь, которая пригодится если надо что - то подсветить или найти потерянную вещь. 
        А когда он ВКонтакте - еще круче!
      </Div>
    </Group>
               
            <Group title="Управление фонариком">
		
        <Div style={{display: 'flex'}}>
        <Button size="xl" level="outline"  onClick={()=>connect.send("VKWebAppFlashSetLevel", {"level": 1}) }>Включить</Button> &#2;
        &#2;<Button size="xl"level="outline"   onClick={()=>connect.send("VKWebAppFlashSetLevel", {"level": 0})}>Выключить</Button>
        </Div>

    </Group>


  

        </Panel>
    );
};

const mapProps = (state) => ({
    top: state.top
});

const mapDispatch = ({ navigator: { goBack }, user: { openProfile } }) => ({
    back: goBack,
    openProfile
});

export default connect(mapProps, mapDispatch)(Top);